  margin: 0;
  padding: 0;
}
html {
  /* height: 100vh; */
}
body {
  width: 100%;
  height: 100vh;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

#greeting,
#title {
  font-size: 80px;
  font-weight: 800;
  margin-top: 10px;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}
#newGame {
  font-size: 20px;
  padding: 8px;
  border-radius: 12px;
  border: none;
  position: relative;
  top: 300px;
}
#newGame:hover {
  transform: scale(1.2);
}

#gatherPlayerInfo {
  display: none;
  font-size: 70px;
  font-weight: 900;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}
input {
  font-size: 30px;
  background-color: rgba(255, 255, 255, 0.4);
}
.opponentContainer {
  display: flex;
  flex-direction: row;
  gap: 40px;
  justify-content: center;
}
.opponent {
  width: 150px;
  font-size: 30px;
  background-color: rgba(134, 133, 133, 0.4);
  border: none;
  padding: 8px;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}

.boardContainer {
  display: none;
  flex-direction: row;
  gap: 50px;
  justify-content: center;

  height: 100vh;
}
#gameBoard1,
#gameBoard2 {
  display: grid;
  /* grid-auto-flow: row; */
  grid-template-columns: repeat(10, 50px);
  grid-template-rows: repeat(10, 50px);
  width: 540px;
  height: 540px;
  flex-wrap: wrap;
}

.boardSquare {
  width: 50px;
  height: 50px;
  background-color: aqua;
  border: solid black 2px;
}
